.bold {
  font-weight: bold; }

.title {
  font-family: "Roboto", sans-serif; }

.about {
  display: flex;
  flex-direction: column;
  background-color: #11B5E4;
  align-content: center;
  width: 100%; }
  .about__title {
    display: flex;
    padding: 20px 0;
    max-width: 1200px;
    border-bottom: 2px solid white;
    font-size: 1.5em;
    margin: 0 20px; }

.about__text {
  display: flex;
  margin: auto;
  font-size: 1em;
  max-width: 800px; }
  @media (max-width: 700px) {
    .about__text {
      flex-direction: column; }
      .about__text iframe {
        height: 350px; } }
  @media (max-width: 460px) {
    .about__text {
      font-size: 30px; }
      .about__text iframe {
        height: 200px; } }
  .about__text p {
    display: flex;
    justify-content: center;
    align-items: center; }
  .about__text iframe {
    margin: 20px; }

.about__content__title {
  font-size: 36px;
  color: white;
  margin: auto; }
  @media (max-width: 460px) {
    .about__content__title {
      font-size: 30px; } }

.about__content__text {
  padding: 20px 20px;
  font-size: 1.6em;
  color: white; }
  @media (max-width: 460px) {
    .about__content__text {
      font-size: 0.6em; } }

.about__links {
  display: flex;
  margin: 20px 0;
  align-content: center;
  justify-content: center;
  font-size: 1.5em;
  text-decoration: none; }

.link__more {
  padding-right: 10px;
  text-decoration: none;
  border-right: 1px solid white; }
  .link__more a {
    color: whitesmoke;
    text-decoration: underline; }
    .link__more a:hover {
      color: orange; }

.link__tutor {
  padding-left: 10px; }
  .link__tutor a {
    text-decoration: none;
    color: whitesmoke;
    text-decoration: underline; }
    .link__tutor a:hover {
      color: orange; }

a {
  text-decoration: none; }

.link {
  text-decoration: none; }
